import React, { useState } from 'react';
import Screen1 from './screen1/screen1'
import Screen2 from './screen2/screen2'
import './App.css';

function App() {
  const [state, setState] = useState({screenNo:1})
  const [lyrics, updateLyrics] = useState("")


  const screenNo = state.screenNo;
  const setScreen = (no) => setState((state) => ({...state, screenNo: no}))

  const setData = (data) => {
    updateLyrics(data)
  }

  return (
    <div className="App">
      <Screen1 screen={screenNo} change={setScreen} data={setData}/>
      <Screen2 lyrics={lyrics} screen={screenNo} change={setScreen}/>
    </div>
  );
}

export default App;
