import React, { useState } from 'react'

export default function Lyrics(props) {
  const [state, setState] = useState({value:""})
  const formatText = (text) => text.replace(/[\r\n]{2,}/g, "\n")

  const handleChange = (event) => {
    let lyrics = event.target.value
    setState({value: lyrics})
    props.process("lyrics", formatText(lyrics))
  }
  
  return (
    <div className="container">
      <label>ENTER YOUR LYRICS</label>
      <div className="lyricsArea">
        <textarea placeholder="Paste your lyrics here" value={state.value} onChange={handleChange}></textarea>
      </div>     
    </div>   
  );
}