import React, {useState, useEffect, useCallback} from "react"

export default function Screen2(props){

  const trigger = (count) => props.change(count)
  const formatLyrics = (text) => text.split("\n").map((value, index) => ({ order: index+1, text:value, start:"", end:"", startSrt:"", endSrt:""}))  
  const convertTime = (duration) => new Date(duration * 1000).toISOString().substr(11,12).replace('.',',')
  const convertSecToTime = (sec) => (sec && typeof sec !== undefined) ? new Date(sec * 1e3).toISOString().substr(11,8) : "" 
    

  function classList(...classes) {
    return classes
      .filter(item => !!item)
      .join(' ');
  }

  const start = () => {
    let audio = document.getElementById('audio-player')
    audio.play()
  }

  const pause = () => {
    let audio = document.getElementById('audio-player')
    audio.pause()  
  }

  
  const [lyrics, setLyricsState] = useState({currentIndex: 0, lines: formatLyrics(props.lyrics)})
    
  let errorMessage = ""
  let lineFinish = false

  function save(filename, text) {
    var element = document.createElement('a')
    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text))
    element.setAttribute('download', filename)
    element.style.display = 'none'
    document.body.appendChild(element)
    element.click()
    document.body.removeChild(element)
  }

  const download = () => {
    
    // check if all the fields are true 
    // otherwise ask for manual entry 

    let emptyCells = lyrics.lines.filter((value, index) => value.start.trim() === "" || value.end.trim() === "")
    if(emptyCells.length) {
      alert('Please fill the empty cells')
      return;
    }

    let outputString = "0\n00:00:00,000 --> 00:00:01,000\nGenerated by: Lyrics Sync"

    lyrics.lines.forEach((line, index) => {
      outputString += `\n\n${1+index}\n${line.startSrt ? line.startSrt : line.start + ',000'} --> ${line.endSrt ? line.endSrt : line.end + ',000'}\n${line.text}`
    })

    save("subtitle.srt",outputString)
  }

  const handleInput = (e, index) => {
    let value = e.target.value
    let name = e.target.name
    setLyricsState(state => {
      let newStateLines = [...state.lines]
      if(name == "start") {
        newStateLines[index] = {...newStateLines[index], start: value}  
      } else if(name == "end") {
        newStateLines[index] = {...newStateLines[index], end: value}  
      }
      let newState = {...state, lines: newStateLines}            
      return newState
    })
  }

  const handleUserKeyPress = useCallback(e => {
    let audio = document.getElementById('audio-player');
    if(props.screen === 1) return false 
    if(e.keyCode == 32) { // space activates the current index, timer starts 
        
      e.stopPropagation();
      e.preventDefault();  
      
      if(audio && audio.currentTime == 0) {
        errorMessage = "Please start the record"
        alert('Please start the record')
        return 0     
      }   

      setLyricsState(state => {
        let newStateLines = [...state.lines]
        let index = newStateLines.findIndex((x) => x.order == state.currentIndex + 1)

        if(index<0) {
          lineFinish = true;
          return state
        }
        newStateLines[index] = {...newStateLines[index], start: convertSecToTime(audio.currentTime), startSrt: convertTime(audio.currentTime)}    
        let newState = {...state, lines: newStateLines, currentIndex: state.currentIndex + 1}
        return newState
      })
      
    } else if (e.keyCode == 13) {

      if(audio && audio.currentTime == 0) {
        errorMessage = "Please start the record"
        alert('please start the record')
        return 0     
      } 

      setLyricsState(state => {
        let newStateLines = [...state.lines]
        let index = newStateLines.findIndex((x) => x.order == state.currentIndex)
        newStateLines[index] = {...newStateLines[index], end: convertSecToTime(audio.currentTime), endSrt: convertTime(audio.currentTime)}   
        let newState = {...state, lines: newStateLines}        
        return newState
      })       
    } 
  }, [props.screen]);

  useEffect(() => {

    let screenNumber = props.screen;
    let audio = document.getElementById('audio-player');
    
    if(audio) {
      audio.pause();
      audio.currentTime = 0;
    }
    
    setLyricsState(state => ({...state, lines: formatLyrics(props.lyrics), currentIndex: 0}))

    window.addEventListener('songLoaded', () => {
      audio = document.getElementById('audio-player')
      audio.addEventListener('play', () => {
        audio.blur()
      })
      audio.addEventListener('pause', () => {
        audio.blur()
      })  
    }, false) 
    
    
    window.addEventListener('keydown', handleUserKeyPress);

    return () => {
      window.removeEventListener('keydown', handleUserKeyPress);
    };
    
  },[props.screen,handleUserKeyPress]);



  return(
    <section className={'screen screen2 active' + props.screen}>
      <header>Sync your lyrics</header>

      <div className="container">
        <div className="lyricsArea">
          <ul>
            {
              lyrics.lines.map((line, index) => {
                return <li key={index} className={classList(lyrics.currentIndex == Number(index+1) && 'active', 'line-'+ Number(index+1), line.start && line.end && 'done')}> 
                  <i>{index+1}</i> 
                  <a>{line.text}</a>
                  <input name="start" value={line.start} onChange={(e) => handleInput(e,index)}></input>
                  <input name="end" value={line.end} onChange={(e) => handleInput(e,index)}></input> 
                </li>
              })
            }  
          </ul>  
        </div>     
      </div> 
      <div className="container">
        <div>
          <button onClick={start}>PLAY</button>
          <button onClick={pause}>PAUSE</button>
          <div id="song"></div>
        </div>
        <div>
          <button onClick={(e) => trigger(1)}>HOMEPAGE</button>
          <button onClick={download}>DOWNLOAD SRT</button>
          <label>{errorMessage}</label>       
        </div>
      </div>
    </section>
  )  
}