import React, { useState, useCallback } from 'react'
import {useDropzone} from 'react-dropzone'


export default function MyDropzone(props) {
  const onDrop = useCallback((acceptedFiles) => {
    acceptedFiles.forEach((file) => {
      const reader = new FileReader()
      reader.onerror = () => console.log('fail on file reading')
      reader.onload = (e) => {
        let present = document.getElementById("audio-player");
        if(present) present.remove() 
        const url = e.target.result;
        const sound = document.createElement("audio");
        const link = document.createElement("source");
        sound.id = "audio-player";
        sound.controls = "controls";
        link.src = url;
        sound.type = "audio/mpeg";
        sound.appendChild(link);
        document.getElementById("song").appendChild(sound);
        const event = new Event('songLoaded');
        window.dispatchEvent(event)
        props.process("song",true)
      }
      reader.readAsDataURL(file)
    })
    
  }, [])
  const {acceptedFiles, getRootProps, getInputProps, isDragActive} = useDropzone({onDrop})

  const files = acceptedFiles.map(file => (
    <li key={file.path}>
      {file.path} - {file.size} bytes
    </li>
  ));

  return (
    <div className="container">
      <label>UPLOAD MP3 FILE</label>
      <div {...getRootProps({className: 'dropzone'})}>
        <input {...getInputProps()} />
        <p>Drag 'n' drop some files here, or click to select files</p>
      </div>
      <aside>
        <h4>Files</h4>
        <ul>{files}</ul>
      </aside>
    </div>
  );    

}