import React, {useState, useCallback} from "react"
import Dragdrop from './drag-drop'
import Lyrics from './lyrics'

export default function Screen1(props){

  const [screen, setScreen] = useState(props.screen);
  const [disable, setVisibility] = useState({dragDrop: true, lyrics: true})
  
  const trigger = (count) => {
    props.change(count)
  }

  const sourceEnable = (type, value) => {
    if(type === "song") {
      setVisibility((disable) => ({...disable,dragDrop: !value}))    
    } else if(type === "lyrics") {
      setVisibility((disable) => ({...disable,lyrics: !Boolean(value && value.trim !== "")}))
      props.data(value.trim())  
    }
  }

  return(
    <section className={'screen screen1 active'+props.screen}>
      <header>Let's start syncing your lyrics</header>
      <Dragdrop process={sourceEnable}/>
      <Lyrics process={sourceEnable}/> 
      <footer>
        <button disabled={disable.lyrics || disable.dragDrop} onClick={(e) => trigger(2)}>LET'S START</button>
      </footer>    
    </section>
  )  
}